








import Vue from 'vue';
import Video from './components/Video.vue';
import Detail from './components/Detail.vue';
import CheckFailed from './components/CheckFailed.vue';
import WebDetailInfo from './components/WebDetailInfo.vue'

export default Vue.extend({
  name: 'App',
  components: {Video, Detail, CheckFailed, WebDetailInfo},
  computed: {
    isShowDetail(): boolean {
      return (this as any).$store.state.isShowDetail;
    },
    checkFailed() {
      return (this as any).$store.state.checkFailed;
    }
  }
});
