


































































export default {
  name: 'BasicInfo',
  data() {
    return {

    }
  },
  computed: {
    scanData() {
      return JSON.parse((window as any).localStorage.getItem('scanData'));
    }
  },
  methods: {
    showDetailInfo() {
      (this as any).$store.state.isShowDetailInfo = true
    },
    openFrame() {
      (window as any).document.querySelector('.frame').appendChild((this as any).$store.state.canvas);
      (this as any).$store.state.isShowFrame = true
    },
    closeFrame() {
      (this as any).$store.state.isShowFrame = false
    }
  }
}
