


















import BasicInfo from './BasicInfo.vue'
import DetailInfo from './DetailInfo.vue'

export default {
  name: 'Detail',
  components:{BasicInfo, DetailInfo},
  data() {
    return {
      
    }
  },
  mounted() {
    
  },
  methods: {
    async goScan() {
      (this as any).$store.state.isShowDetail = false;
      (this as any).$store.state.isShowDetailInfo = false;
      await (this as any).$store.state.scanner.open();
    }
  },
  computed: {
    isShowDetailInfo() {
      return (this as any).$store.state.isShowDetailInfo;
    },
    scanData() {
      return (this as any).$store.state.scanData;
    }
  }
}
