let request = async (file: string|object, context: any) => {
  let results
  let reader = await (window as any).Dynamsoft.DBR.BarcodeReader.createInstance();
  reader.ifSaveOriginalImageInACanvas = true;
  if(typeof file === 'object') {
    results = await reader.decode(file);
    context.$store.state.canvas = reader.getOriginalImageInACanvas()
  } else {
    results = await reader.decodeUrl(file);
    context.$store.state.canvas = reader.getOriginalImageInACanvas()
  }
  for(let result of results){
    let rep = await fetch("http://localhost/desktop/dbrjs-dip-dnet/parseId.ashx",{
      method: "POST",
      body: result.barcodeText
    });
    if(!rep.ok){
      throw new Error("Network error: "+rep.status+" "+rep.statusText);
    }
    let obj = await rep.json();
    if(!obj.success){
      context.$store.state.isVDS = true
      setTimeout(() => {
        context.$store.state.isVDS = false
      },3000)
      throw new Error(obj.message);
    }
    (window as any).localStorage.setItem('scanData', JSON.stringify(obj.data))
    context.$store.state.scanData = obj.data;
    context.$store.state.isShowDetail = !context.$store.state.isShowDetail
  }
}

export default request