



































































import request from '../request' 
export default {
  name: 'HomeTopTab',
  data() {
    return {
      cameraResolution: ['1280*720', '1920*1080'],
    }
  },
  mounted() {

  },
  methods: {
    openCameraList():void {
      (this as any).$store.state.listIsOpen = !(this as any).$store.state.listIsOpen;
      (this as any).$store.state.isOpenCameraList =  !(this as any).$store.state.isOpenCameraList;
      if((this as any).isOpenResolutionList || (this as any).isOpenMethodList) {
        (this as any).$store.state.isOpenResolutionList =  false;
        (this as any).$store.state.isOpenMethodList =  false;
      }
    },
    openResolutionList():void {
      (this as any).$store.state.isOpenResolutionList = !(this as any).$store.state.isOpenResolutionList;
      if((this as any).isOpenCameraList || (this as any).isOpenMethodList) {
        (this as any).$store.state.isOpenCameraList =  false;
        (this as any).$store.state.isOpenMethodList =  false;
      }
    },
    async changeCamera(info: any) {
      if(info.label === (this as any).currentCamera.label) {
        return
      } else {
        await (this as any).$store.state.scanner.updateVideoSettings({
          video: {
            deviceId:info.deviceId          
          }
        });
        let currentCameraLabel = await (this as any).scannerInstance.getCurrentCamera();
        (this as any).$store.state.currentCamera = currentCameraLabel;
        (this as any).$store.state.currentCameraLabel = currentCameraLabel.label;
      }
    },

    async changeResolution(info: any) {
      console.log(info);
    },

    showInfo():void {
      if((window as any).localStorage.getItem('scanData')) {
        if((window as any).document.body.clientWidth >= 980) {
          (this as any).$store.state.isShowWebDetailInfo = true;
        } else {
          (this as any).$store.state.isShowDetail = true;
        }
      } else {
        alert('Please scan the QR code for vaccination information first');
      }
    },

    uploadImg():void {
      (this as any).$store.state.isOpenMethodList = !(this as any).$store.state.isOpenMethodList;
      if((this as any).isOpenCameraList || (this as any).isOpenResolutionList) {
        (this as any).$store.state.isOpenCameraList =  false;
        (this as any).$store.state.isOpenResolutionList =  false;
      }
    },

    async uploadLocalImgAndRead(e: any) {
      request(e.target.files[0], this)
    },
    
    async uploadSampleImgAndRead() {
      request('http://localhost/desktop/dcp-demo/public/sample-img.png', this)
    }
  },

  computed: {
    scannerInstance() {
      return (this as any).$store.state.scanner;
    },
    cameraList(): Array<Object> {
      return (this as any).$store.state.cameraList;
    },
    currentCamera(): Object {
      return (this as any).$store.state.currentCamera;
    },
    isOpenCameraList(): boolean {
      return (this as any).$store.state.isOpenCameraList;
    },
    isOpenResolutionList(): boolean {
      return (this as any).$store.state.isOpenResolutionList;
    },
    currentResolution(): Array<Object>{
      return (this as any).$store.state.currentResolution.join('*');
    },
    isOpenMethodList(): boolean {
      return (this as any).$store.state.isOpenMethodList;
    }
  }
}
