

















































































































import HomeTopTab from './HomeTopTab.vue'
import WebDetailInfo from './WebDetailInfo.vue'

export default {
  name: 'Video',
  components:{HomeTopTab, WebDetailInfo},
  data() {
    return {
      textDisplay: false,
    }
  },
  mounted() {
    (async () => {
      let scanner = await (window as any).Dynamsoft.DBR.BarcodeScanner.createInstance();
      (this as any).$store.state.scanner = scanner
      await (this as any).scannerInstance.setUIElement(document.querySelector('.video-container'));
      (this as any).scannerInstance.ifSaveOriginalImageInACanvas = true;
      if((window as any).document.body.clientWidth >= 980) {
        (this as any).$store.state.isShowCameraName = true;
        let settings = await (this as any).scannerInstance.getRuntimeSettings();
        settings.region.regionMeasuredByPercentage = 1;
        settings.region.regionLeft = 30;
        settings.region.regionTop = 15;
        settings.region.regionRight = 70;
        settings.region.regionBottom = 65;
        await (this as any).scannerInstance.updateRuntimeSettings(settings);
      } else {
        (this as any).$store.state.isShowCameraName = false;
        let settings = await (this as any).scannerInstance.getRuntimeSettings();
        settings.region.regionMeasuredByPercentage = 1;
        settings.region.regionLeft = 5;
        settings.region.regionTop = 15;
        settings.region.regionRight = 95;
        settings.region.regionBottom = 65;
        await (this as any).scannerInstance.updateRuntimeSettings(settings);
      }
      (window as any).onresize = async () => {
        if((window as any).document.body.clientWidth >= 980) {
          (this as any).$store.state.isShowCameraName = true;
          let settings = await (this as any).scannerInstance.getRuntimeSettings();
          settings.region.regionMeasuredByPercentage = 1;
          settings.region.regionLeft = 30;
          settings.region.regionTop = 15;
          settings.region.regionRight = 70;
          settings.region.regionBottom = 65;
          await (this as any).scannerInstance.updateRuntimeSettings(settings);
          if((this as any).$store.state.isShowDetail === true) {
            (this as any).$store.state.isShowDetail = false;
            (this as any).$store.state.isShowWebDetailInfo = true;
            (this as any).$store.state.isShowFrame = false;
          }
        } else {
          (this as any).$store.state.isShowCameraName = false;
          let settings = await (this as any).scannerInstance.getRuntimeSettings();
          settings.region.regionMeasuredByPercentage = 1;
          settings.region.regionLeft = 5;
          settings.region.regionTop = 15;
          settings.region.regionRight = 95;
          settings.region.regionBottom = 65;
          await (this as any).scannerInstance.updateRuntimeSettings(settings);
          if((this as any).$store.state.isShowWebDetailInfo === true) {
            (this as any).$store.state.isShowDetail = true;
            (this as any).$store.state.isShowWebDetailInfo = false;
            (this as any).$store.state.isShowFrame = false;
          }
        }
      }

      (this as any).scannerInstance.onFrameRead = (results: string) => {};

      (this as any).scannerInstance.onUnduplicatedRead = async (txt: string, result: any) => {
        (this as any).$store.state.canvas = (this as any).scannerInstance.getOriginalImageInACanvas()
        let rep = await fetch("https://192.168.8.183/desktop/dbrjs-dip-dnet/parseId.ashx",{
          method: "POST",
          body: txt
        });
        console.log(rep); 
        if(!rep.ok){
          throw new Error("Network error: "+rep.status+" "+rep.statusText);
        }
        
        let obj = await rep.json();
        console.log(obj);
        if(!obj.success){
          (this as any).$store.state.isVDS = true
          setTimeout(() => {
            (this as any).$store.state.isVDS = false
          },3000)
          throw new Error(obj.message);
        }
        (this as any).$store.state.scanData = obj.data;
        if((window as any).document.body.clientWidth >= 980) {
          (this as any).$store.state.isShowWebDetailInfo = !(this as any).$store.state.isShowWebDetailInfo
        } else {
          (this as any).$store.state.isShowDetail = !(this as any).$store.state.isShowDetail;
        }
        (window as any).localStorage.setItem('scanData', JSON.stringify(obj.data))
        await (this as any).scannerInstance.close();
      };
      
      await (this as any).scannerInstance.open();
      (this as any).textDisplay = true;
      let cameras = await (this as any).scannerInstance.getAllCameras();
      let currentCamera = await (this as any).scannerInstance.getCurrentCamera();
      let rsl = await (this as any).scannerInstance.getResolution();
      (this as any).$store.state.cameraList = cameras;
      (this as any).$store.state.currentCamera = currentCamera;
      (this as any).$store.state.currentCameraLabel = (this as any).$store.state.currentCamera.label as any
      (this as any).$store.state.currentResolution = rsl;
    })();
  },
  computed: {
    scannerInstance() {
      return (this as any).$store.state.scanner;
    },
    isVDS() {
      return (this as any).$store.state.isVDS;
    }
  },
  methods: {
    closeList() {
      (this as any).$store.state.isOpenCameraList =  false;
      (this as any).$store.state.isOpenResolutionList = false;
      (this as any).$store.state.isOpenMethodList = false;
    }
  }
}
